.App-header {
  text-align: center;
  height: 80px;
  padding: 20px;
  color: white;
}

.App-header div {
  margin-top: 0;
  font-weight: 600;
  color: #eee;
}

.App-title {
  text-align: center;
  font-size: 1.5em;
  margin-bottom: 0em;
  padding-bottom: 0em;
  font-weight: lighter;
  vertical-align: center;
}

.App-intro {
  margin-top: 3em;
  text-align: center;
  font-size: large;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.span-right {
  float: right;
  font-size: 0.75em;
  cursor: pointer;
  margin-right: 0.5em;
}

a {
  text-decoration: none;
  color: #2196f3;
}

a:visited {
  color: #2196f3;
}

.strong {
  font-weight: bold;
}

ol {
  padding-left:0;
}â€‹

.span-right:first-child {
  margin-right: 0em;
}

.span-left {
  float: left;
  font-size: 0.75em;
}

.app-bar-icon-right {
  display: block;
}

.app-menu-item {
  display: none;
}

.text-left {
  text-align: left;
}

.font-small {
  font-size: 1em;
}

.citation {
  background-color: #eee;
}

table td {
  padding: 5px;
  padding-left: 2px;
  text-align: left;
}

table tr > td:first-child {
  white-space: nowrap;
}

@media only screen and (min-width: 768px) {
  .App-title {
    font-size: 2.5em;
  }

  .App-header {
    height: 100px;
  }

  .App-intro {
    width: 80%;
  }

  .app-bar-icon-right {
    display: none !important;
  }

  .app-menu-item {
    display: inline;
  }
}

@media only screen and (min-width: 1024px) {
  .App-title {
    font-size: 3.5em;
  }

  .App-intro {
    width: 60%;
  }

  .App-header {
    height: 130px;
  }

  .span-left {
    font-size: 0.8em;
  }

  .span-right {
    font-size: 0.8em;
  }
}

@media only screen and (min-width: 1440px) {
  .App {
  }
  .App-header {
    height: 150px;
  }

  .App-intro {
    width: 50%;
  }
  .App-title {
    font-size: 4em;
  }

  .text {
    width: 50%;
    margin: 0 auto;
    text-align: justify;
  }
}



